import {
	createRouter,
	createWebHashHistory
} from 'vue-router'

const routes = [
	{
		path: '/',
		name: '/',
		props: true,
		component: () => import('../views/homePage.vue'),
		children: [{
			path: "/add",
			props: true,
			component: () => import("../components/ShowCard.vue")
		}]
	},
	{
		path: '/shoppingcar',
		name: 'shoppingcar',
		component: () => import('../views/ShoppingCar.vue')
	},
	{
		path: '/home',
		name: 'home',
		redirect: {
			path: "/"
		}
	},
	{
		path: '/mine',
		name: 'mine',
		component: () => import('../views/Mine-page.vue')
	},
	{
		path: '/address',
		name: 'address',
		component: () => import('../views/addAddress.vue')
	},
	{
		path: '/order',
		name: 'order',
		component: () => import('../views/confirmOrder.vue')
	},
	{
		path: '/addresslist',
		name: 'addresslist',
		component: () => import('../views/addressList.vue')
	}
]

const router = createRouter({
	history: createWebHashHistory(),
	routes
})

export default router
