import { ref } from 'vue';
export default {
  setup() {
    const active = ref(0);
    const images = [{
      img: require("@/assets/shouye.png"),
      tit: "首页",
      href: "/home"
    }, {
      img: require("@/assets/gouwuche.png"),
      tit: "购物车",
      href: "/shoppingcar"
    }, {
      img: require("@/assets/wode.png"),
      tit: "我的",
      href: "/mine"
    }];
    return {
      active,
      images
    };
  },

  methods: {
    link() {
      if (event.path.length == 10 || event.path.length == 11) {
        switch (event.path[0].innerText) {
          case "首页":
            this.$router.push({
              path: "/home"
            });
            break;

          case "购物车":
            this.$router.push({
              path: "/shoppingcar"
            });
            break;

          case "我的":
            this.$router.push({
              path: "/mine"
            });
            break;
        }
      } else if (event.path.length == 13) {
        switch (event.path[3].innerText) {
          case "首页":
            this.$router.push({
              path: "/home"
            });
            break;

          case "购物车":
            this.$router.push({
              path: "/shoppingcar"
            });
            break;

          case "我的":
            this.$router.push({
              path: "/mine"
            });
            break;
        }
      }
    },

    change(e) {
      localStorage.setItem('active', e);
    }

  },

  beforeCreate() {
    console.log(this.$route.path);

    switch (this.$route.path) {
      case "/home":
        localStorage.setItem('active', 0);
        break;

      case "/shoppingcar":
        localStorage.setItem('active', 1);
        break;

      case "/mine":
        localStorage.setItem('active', 2);
        break;
    }
  },

  created() {
    this.active = JSON.parse(localStorage.getItem('active'));
  }

};