import tabbar from "./components/TabBar.vue";
export default {
  data() {
    return {};
  },

  components: {
    tabbar
  },

  beforeCreate() {// console.log("vue实例创建前")
  },

  created() {// console.log("vue实例创建后")
  },

  beforeMount() {// console.log("页面渲染前,dom结构还未出现")
  },

  mounted() {
    // console.log("页面渲染后,已出现dom结构")
    this.$nextTick(() => {
      document.addEventListener('touchstart', function (event) {
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      });
      document.addEventListener('gesturestart', function (event) {
        event.preventDefault();
      });
    });
    console.log(this.$route, 123);
  },

  beforeUpdate() {// console.log("页面更新前")
  },

  updated() {// console.log("页面更新后")
  }

};